// centring
@mixin сentering-xy() { 
	position: absolute; 
	left: 50%; top: 50%; 
	transform: translate(-50%, -50%); 
} 

@mixin сentering-x() { 
	position: absolute; 
	left: 50%; 
	transform: translateX(-50%); 
} 

@mixin сentering-y() { 
	position: absolute; 
	top: 50%; 
	transform: translateY(-50%); 
}

// clearfix
@mixin clearfix { 
	&:before, 
	&:after { 
		display: table; 
		content: " "; 
	} 

	&:after { 
		clear: both; 
	} 
}
// box-shadow
@mixin box-shadow($top, $left, $blur, $color, $inset:"") {
  -webkit-box-shadow:$top $left $blur $color #{$inset};
     -moz-box-shadow:$top $left $blur $color #{$inset};
          box-shadow:$top $left $blur $color #{$inset};
}

// border-radius
@mixin border-radius($radius) {
  -webkit-border-radius: $radius;
     -moz-border-radius: $radius;
      -ms-border-radius: $radius;
          border-radius: $radius;
}

// pseudo-element
@mixin pseudo-element($content: '', $display: block, $pos: absolute){ 
	content: $content; 
	display: $display; 
	position: $pos; 
}

// Font size mixin
$font-lock-min:  567px !default; 
$font-lock-max: 1500px !default;

//  font-size 
@mixin font-size(
  $min,                         // font-size 
  $max: null,                   // optional maximum font size
  $lock-min: $font-lock-min,  // optional minimum viewport width 
  $lock-max: $font-lock-max   // optional maximum viewport width 
  ){
    & { font-size: $min; }
    @if $max {
      @media (min-width: $lock-min) and (max-width: $lock-max){ 
        & { 
          font-size: calc(
            #{$min} + 
            #{num($max) - num($min)} * 
            (100vw - #{$lock-min}) / 
            #{num($lock-max) - num($lock-min)}
          ); 
        } 
      }
      @media (min-width: $lock-max){ 
        & { font-size: $max; } 
      }
    }
}
@function num($input){  @return $input/($input * 0 + 1); }

// font-family
@mixin font-family-style ($size: false, $color: false, $weight: false, $lh: false) { 
	font-family: 'Open Sans', sans-serif;
	@if $size { 
		font-size: $size;
	} 
	@if $color { 
		color: $color; 
	} 
	@if $weight { 
		font-weight: $weight; 
	}
	@if $lh { 
		line-height: $lh; 
	} 
}

// link-color
@mixin link ($link: false, $hover: false, $active: false, $visit: false) { 
	a {
		text-decoration: none;
		color: $link;

		&:hover { 
			color: $hover; 
		}

		&:active { 
			color: $active; 
		}

		&:visited { 
			color: $visit; 
		}
	} 
}

// vertical align
@mixin ghostVerticalAlign(){
  &:before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%; width: .1px;
  }
}

// Media queries
$base-font-size: 16px !default;

@mixin respond-from($value, $respond-direction: width, $orientation: null) {
  // Convert pixel in em
  $value-em: ($value / $base-font-size) + 0em;

  @if $orientation !=null {
    @media (min-#{$respond-direction}: $value-em) and (orientation: $orientation) {
      @content;
    }
  } @else {
    @media (min-#{$respond-direction}: $value-em) {
      @content;
    }

    // Prevent Error
    @if $respond-direction !="width" and $respond-direction !="height" {
      @error "Wrong direction : must be width or height";
    }
    @elseif $orientation != null and $orientation !="portrait" and $orientation !="landscape" {
    @error "Wrong orientation : must be portrait or landscape";
  }
  }
}

@mixin respond-until($value, $respond-direction: width, $orientation: null) {
  // Convert pixel in em
  $value-em: ($value / $base-font-size) + 0em;

  @if $orientation !=null {
    @media (max-#{$respond-direction}: $value-em) and (orientation: $orientation) {
      @content;
    }
  }  @else {
    @media (max-#{$respond-direction}: $value-em) {
      @content;
    }
  }

  // Prevent error
  @if $respond-direction !="width" and $respond-direction !="height" {
    @error "Wrong direction : must be width or height";
  }
  @elseif $orientation != null and $orientation !="portrait" and $orientation !="landscape" {
    @error "Wrong orientation : must be portrait or landscape";
  }
}

@mixin respond-between($value1, $value2, $respond-direction: width, $orientation: null) {
  $value1-em: ($value1 / $base-font-size) + 0em;
  $value2-em: ($value2 / $base-font-size) + 0em;
  @if $orientation !=null {
    @media (min-#{$respond-direction}: $value1-em) and (max-#{$respond-direction}: $value2-em) and (orientation: $orientation) {
      @content;
    }
  }  @else {
    @media (min-#{$respond-direction}: $value1-em) and (max-#{$respond-direction}: $value2-em) {
      @content;
    }
  }

  // Prevent error
  @if $respond-direction !="width" and $respond-direction !="height" {
    @error "Wrong direction : must be width or height";
  }
  @elseif $orientation != null and $orientation !="portrait" and $orientation !="landscape" {
    @error "Wrong orientation : must be portrait or landscape";
  }
}

